.slide-top {
    -webkit-animation: slide-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940),
        slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 3s;
    animation: slide-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940),
        slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 3s;
}

.slide-bottom {
    -webkit-animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940);
    animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940);
}

@keyframes slide-top {
    0% {
        -webkit-transform: translateY(100px);
        transform: translateY(100px);
    }
    100% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px);
    }
}

@keyframes slide-bottom {
    0% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px);
    }
    100% {
        -webkit-transform: translateY(100px);
        transform: translateY(100px);
    }
}

@keyframes growDown {
    0% {

        transform: scaleY(0)
    }
    80% {

        transform: scaleY(1.1)
    }
    100% {

        transform: scaleY(1)
    }
}